

















































/**
 * 后台左侧菜单
 */
import {Component, Vue, Watch} from 'vue-property-decorator';
import {appRouter} from '@/router'
import {traversingTree} from '@/util'
import _ from 'lodash'


@Component
export default class MenuList extends Vue {
    //移除不是菜单的路由
    menuList = appRouter.filter((item: any) => {
        if (item.meta && item.meta.isMenu) {
            return item
        }
    });

    //通过权限过滤的菜单
    get menuListAuc() {
        let res = _.cloneDeep(this.menuList);
        traversingTree(res, {
            childList: 'children',
            filterFun: (item: any) => {
                if (item.meta) {
                    //菜单地址转换为权限码
                    item.meta.isShow = this.$SSO_AUC(_.camelCase(_.replace(item.path, '/', '_')))
                }
            }
        })
        return res
    }

    defaultActive: string = '';//默认激活索引

    openMenuArray = []

    // 默认系统图标
    logoUrl: any = require('@/assets/images/logo.png')

    get getLogo() {
        if (this.$store.state.SYSTEM_INFO.logo) {
            return 'data:image/jpeg;base64,' + this.$store.state.SYSTEM_INFO.logo
        } else {
            return this.logoUrl
        }
    }

    @Watch('$route.path', {
        immediate: true
    })
    routeChange() {
        if (this.$route.matched[1]) {
            this.defaultActive = this.$route.matched[1].path;
        }
    }

    toSelectSys() {

    }
}
